.dashboard-container {
    display: flex;
    gap: 20px;
  }
  
  .sidebar {
    background: var(--secondary-color);
    color: white;
    padding: 20px;
    border-radius: 10px;
    width: 250px;
  }
  
  .main-content {
    flex: 1;
    background: var(--card-bg);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  