.hero {
    text-align: center;
    padding: 50px;
    /* background: var(--primary-color); */
    color: white;
    border-radius: 10px;
  }
  
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .features {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 30px;
  }
  
  .feature-card {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    flex: 1;
    text-align: center;
  }
