.login-container {
    max-width: 400px;
    margin: auto;
    background: var(--card-bg);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .login-container h2 {
    margin-bottom: 15px;
    color: var(--primary-color);
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .login-form input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-button {
    background: var(--primary-color);
    color: white;
    padding: 12px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .login-button:hover {
    background: var(--button-hover);
  }
  
  .register-link {
    margin-top: 10px;
    font-size: 0.9rem;
  }
  
  .register-link a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  .register-link a:hover {
    text-decoration: underline;
  }
#container{
  margin: 100px auto 200px;
  width: 1000px;
}