/* Courses.css */
.courses-container {
  max-width: 1200px;
  margin: auto;
  padding: 50px 200px 200px;
}

h1 {
  text-align: center;
  margin-bottom: 50px;
}

.filter-container {
  display: none;
  justify-content: space-between;
  margin-bottom: 20px;
}

.filter-container select, .filter-container button {
  padding: 10px;
  font-size: 16px;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
}

.course-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 20px;
  border-radius: 8px;
  background-color: #f9f9f9;
  text-align: center;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background: #ddd;
  border-radius: 5px;
  margin: 10px 0;
}

.progress {
  height: 100%;
  background: #4caf50;
  border-radius: 5px;
}

.view-button {
  display: inline-block;
  padding: 10px 15px;
  background: #007bff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 30px;
}
h3{
  margin-bottom: 30px;
}
p{
  margin-bottom: 0;
}